import qrcode from './qrcode';//

// 
let settingWritePhotosAlbum = false;
export const saveImg = function(url,callback) {
	if (url) {
		console.log('url',url);

		if (settingWritePhotosAlbum) {
			uni.getSetting({
				success: res => {
					if (res.authSetting['scope.writePhotosAlbum']) {
                        uni.showLoading({
                            title: ''
                        });
						uni.downloadFile({
							url: url,
							success: data => {
								console.log('data',data);
								if (data.statusCode == 200) {
								
									uni.saveImageToPhotosAlbum({
										filePath: "https://img.alicdn.com/imgextra/i2/195729828/O1CN01ip7ZbJ2MTHGyT6WoI_!!195729828.jpg",
										success: () => {
                                            uni.hideLoading();
											callback && callback();
											uni.showToast({
												title: ''
											});
										},
                                        fail(e) {
											console.log("e",e);
                                            uni.hideLoading();
                                            tip({
                                                title: '' + e.errMsg,
                                                icon: "none"
                                            });
                                        }
									});
								} else {
                                    uni.hideLoading();
                                    uni.showToast({
                                    	title: '',
                                        icon: "none"
                                    });
                                }
							},
                            fail(e) {
                                uni.hideLoading();
                                uni.showToast({
                                	title: '' + e.errMsg,
                                    icon: "none"
                                });
                            }
						});
					} else {
						uni.showModal({
							title: '',
							content: '',
							confirmText: '',
							cancelText: '',
							success: data => {
								if (data.confirm) {
									uni.openSetting();
								}
							}
						});
					}
				}
			});
		} else {
			settingWritePhotosAlbum = true;
			uni.authorize({
				scope: 'scope.writePhotosAlbum',
				success: () => {
                    uni.showLoading({
                        title: ''
                    });
					uni.downloadFile({
						url: url,
						success: data => {
							if (data.statusCode == 200) {
								uni.saveImageToPhotosAlbum({
									filePath: data.tempFilePath,
									success: () => {
                                        uni.hideLoading();
										callback && callback();
										uni.showToast({
											title: ''
										});
									},
                                    fail(e) {
                                        uni.hideLoading();
                                        tip({
                                            title: '' + e.errMsg,
                                            icon: "none"
                                        });
                                    }
								});
							} else {
                                uni.hideLoading();
                                uni.showToast({
                                    title: '',
                                    icon: "none"
                                });
                            }
                        },
                        fail(e) {
                            uni.hideLoading();
                            uni.showToast({
                                title: '' + e.errMsg,
                                icon: "none"
                            });
                        }
					});
				}
			});
		}


















































	} else {
		uni.showToast({
			title: '',
			icon: 'none'
		});
	}
};

// 
export const qrcodeCanvas = function(id, code, width, height, that) {
    qrcode.api.draw(code, {
        ctx: uni.createCanvasContext(id, that),
        width: convert_length(width),
        height: convert_length(height)
    })
}
function convert_length(length) {
    return Math.round(uni.getSystemInfoSync().windowWidth * length / 750);
}