/*
 * ---2022.7.22
 * family = " 'PingFang SC',tahoma,arial,'helvetica neue','hiragino sans gb','microsoft yahei',sans-serif";
 * var options = {
				font:"22px" + family,  
				ctx:ctx,          uni.createCanvasContext('firstCanvas')
				word:"",      
				maxWidth:750,     
				maxLine:2,        
				x:100,            x
				y:100,            y
				l_h:40,           
				textCenter:false, 
				cvsW:cvsW,        --
			}
 * callback 
 */
function dealWords(options, callback) {
    options.ctx.font = options.font; //
    callback && callback();
	var allRow = getLineNum(options.ctx,options.word,options.maxWidth)
    // var allRow = Math.ceil(options.ctx.measureText(options.word).width / options.maxWidth)+1; //--
    var count = allRow >= options.maxLine ? options.maxLine : allRow; //
    var endPos = 0; //
    for (var j = 0; j < count; j++) {
		if(endPos>=options.word.length){
			break;
		}
        var nowStr = options.word.slice(endPos); //
        var rowWid = 0; //    
        if (options.ctx.measureText(nowStr).width > options.maxWidth) { //
            for (var m = 0; m < nowStr.length; m++) {
                rowWid += options.ctx.measureText(nowStr[m]).width; //
                if (rowWid > options.maxWidth) {
                    endPos += m; //
					
					var isBlock = false //
					var num = 1 //'',
					// --
					var jy = /^[a-zA-Z]*$/
					if(jy.test(nowStr[m-1])){
						console.log(m-1,nowStr[m-1],'')
						for (var n = 1; n < (m-1); n++) {
							if(!jy.test(nowStr[m-1-n])){
								console.log(m-1-n,nowStr[m-1-n],num,endPos,'')
								endPos = endPos-num
								isBlock = true
								break;
							}
							num++
						}
					}
					if (j === options.maxLine - 1) { //
						options.ctx.fillText(nowStr.slice(0, m - 1) + '...', options.textCenter?(options.cvsW-options.ctx.measureText(nowStr.slice(0, m - 1)).width)/2:options.x, options.y + (j + 1) * options.l_h); //(j+1)*18 
					} else {
					    options.ctx.fillText(nowStr.slice(0, isBlock?(m - num):m), options.textCenter?(options.cvsW-options.ctx.measureText(nowStr.slice(0, isBlock?(m - num):m)).width)/2:options.x, options.y + (j + 1) * options.l_h);
					}
                    break;
                }
            }
        } else { //
            options.ctx.fillText(nowStr.slice(0), options.textCenter?(options.cvsW-options.ctx.measureText(nowStr.slice(0)).width)/2:options.x, options.y + (j + 1) * options.l_h);
        }
    }
}

/*
 * --
 * ctx:cxt;
 * str:
 * maxWidth:
 */
function getLineNum(ctx,str,maxWidth){
	var allRow = Math.ceil(ctx.measureText(str).width / maxWidth)+1; //--
	var endPos = 0; //
	var lineNum = 0 //
	for (var j = 0; j < allRow; j++) {
	    var nowStr = str.slice(endPos); //
	    var rowWid = 0; //    
	    if (ctx.measureText(nowStr).width > maxWidth) { //
	        for (var m = 0; m < nowStr.length; m++) {
	            rowWid += ctx.measureText(nowStr[m]).width; //
	            if (rowWid > maxWidth) {
	                endPos += m; //
					
					var isBlock = false //
					var num = 1 //'',
					// --
					var jy = /^[a-zA-Z]*$/
					if(jy.test(nowStr[m-1])){
						console.log(m-1,nowStr[m-1],'')
						for (var n = 1; n < (m-1); n++) {
							if(!jy.test(nowStr[m-1-n])){
								console.log(m-1-n,nowStr[m-1-n],num,endPos,'')
								endPos = endPos-num
								isBlock = true
								break;
							}
							num++
						}
					}
	                break;
	            }
	        }
	    } else {
			lineNum = j+1
			console.log(nowStr.slice(0),lineNum,j,'')
	        break;
	    }
	}
	console.log(lineNum,'')
	return lineNum
}


/*
 * 
 * ctx:createCanvasContext
 * color:;
 * x:x
 * y:y
 * width:
 * height:
 * radius:
 * text:
 * fontColor:
 * textAlign: left/center/right
 */
function drawButton(ctx, color, x, y, width, height, radius, text, fontColor, textAlign) { 
    //44
    ctx.beginPath();
    ctx.fillStyle = color;
    ctx.moveTo(x + radius, y);
    ctx.lineTo(x + width - radius, y);
    ctx.arc(x + width - radius, y + radius, radius, Math.PI * 3 / 2, Math.PI * 2);
    ctx.lineTo(x + width, y + height - radius);
    ctx.arc(x + width - radius, y + height - radius, radius, Math.PI, Math.PI / 2);
    ctx.lineTo(x + radius, y + height);
    ctx.arc(x + radius, y + height - radius, radius, Math.PI / 2, Math.PI);
    ctx.lineTo(x, y + radius);
    ctx.arc(x + radius, y + radius, radius, Math.PI, Math.PI * 3 / 2);
    ctx.fill();
    ctx.closePath();

    ctx.beginPath();
    ctx.fillStyle = fontColor;
    // ctx.font = 'normal bold 12px sans-serif';
    ctx.setTextAlign(textAlign)
    ctx.textBaseline = "middle";
    ctx.fillText(text, x + width / 2, y + height / 2);
}

export default {
    fillRoundRect:fillRoundRect, //
    roundRectBorder:roundRectBorder,//+
    getImageInfo:getImageInfo, //
    drawCircular:drawCircular, //
    drawImgCover:drawImgCover, //cover
    dealWords:dealWords, //
    drawButton:drawButton, //
	getLineNum:getLineNum, //
}